import { get, set } from "firebase/database";
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { userCountsRef } from "../firebase";

const pageLinks = [
    { path: "/calculate-shanten", title: "", description: "" },
    { path: "/efficiency-trainer", title: "", description: "" },
    { path: "/pure-ones-efficiency-trainer", title: "", description: "" },
    { path: "/pure-ones-ready-trainer", title: "", description: "" },
    { path: "/points-calculate-trainer", title: "", description: "" },
    { path: "/aotenjiuu-calculate-trainer", title: "", description: "" },
    { path: "/fu-calculate-trainer", title: "", description: "" },
    { path: "/comments", title: "", description: "" },
];

const MainPage = (props) => {
    const [userCounts, setUserCounts] = useState(null);

    // 
    useEffect(() => {
        get(userCountsRef)
            .then((snapshot) => {
                if (snapshot.exists()) {
                    setUserCounts(snapshot.val() + 1);
                    set(userCountsRef, snapshot.val() + 1);
                } else {
                    console.log("");
                }
            })
            .catch((error) => {
                console.error(":", error);
            });
    }, []);

    // 
    useEffect(() => {
        fetch(`${process.env.PUBLIC_URL}/images.json`)
            .then((res) => res.json())
            .then((data) => {
                data.images.forEach((filename) => {
                    const img = new Image();
                    img.src = `${process.env.PUBLIC_URL}/images/${filename}`;
                });
            });
    }, []);

    return (
        <div
            style={{
                textAlign: "center",
                maxWidth: "80%",
                margin: "auto", // 
                display: "flex", //  Flexbox
                flexDirection: "column", // 
                alignItems: "center", // 
                justifyContent: "center", // 
            }}
        >
            <div style={{ color: "#007BFF", fontSize: "clamp(28px, 2vw, 32px)", fontWeight: "bold" }}></div>
            <div
                style={{
                    display: "grid",
                    gridTemplateColumns: "repeat(2, 1fr)",
                    gap: "15px",
                    justifyContent: "center",
                    marginTop: "20px",
                    gridAutoFlow: "row dense",
                }}
            >
                {pageLinks.map(({ path, title, description }, index) => {
                    const isLastSingle = pageLinks.length % 2 === 1 && index === pageLinks.length - 1;

                    return (
                        <Link
                            key={path}
                            to={path}
                            style={{
                                textDecoration: "none",
                                padding: "15px",
                                borderRadius: "10px",
                                border: "1px solid #007BFF",
                                color: "#007BFF",
                                transition: "background-color 0.3s, color 0.3s",
                                display: "block",
                                backgroundColor: "#f8f9fa",
                                textAlign: "center",
                                fontSize: "24px",
                                fontWeight: "bold",
                                cursor: "pointer",
                                position: "relative",
                                gridColumn: isLastSingle ? "span 2" : "auto", // 
                            }}
                        >
                            <div style={{ fontSize: "clamp(20px, 2vw, 24px)", fontWeight: "bold" }}>{title}</div>
                            {description && <p style={{ fontSize: "calc(0.8 * clamp(20px, 2vw, 24px))", fontWeight: "normal", margin: "10px 0 0" }}>{description}</p>}
                            <style>
                                {`
                                a:hover {
                                    background-color: #007BFF !important;
                                    color: #fff !important;
                                }
                                @media (max-width: 1024px) {
                                    div {
                                        grid-template-columns: repeat(2, 1fr);
                                    }
                                }
                                @media (max-width: 768px) {
                                    div {
                                        grid-template-columns: repeat(1, 1fr);
                                    }
                                }
                            `}
                            </style>
                        </Link>
                    );
                })}
            </div>

            {userCounts && <div style={{ fontSize: "clamp(16px, 2vw, 20px)", fontWeight: "bold", marginTop: "30px" }}> {userCounts} </div>}
        </div>
    );
};

export default MainPage;
